function permute(str){
    return permuteArr(str.split(''))
}

function flatten(arr){
    return arr.reduce((acc, curr)=> acc.concat(curr), [])
}

/*function withoutArr(arr, a){
    let result = [...arr]
    result.splice(arr.indexOf(a), 1)
    return result
}*/

function withoutArr(array, a) {
    const bs = array.slice(0)
    bs.splice(array.indexOf(a), 1)
    return bs
}


function permuteArr(arr){
    switch(arr.length){
        case 0:
            return []
        case 1:
            return arr
        default: return flatten(arr.map(a=>permuteArr(withoutArr(arr, a)).map(b=>b.concat(a))))
    }
}

// tests
import { test } from 'ava'

test(t => t.deepEqual(permute(''), []))
test(t => t.deepEqual(permute('abc'), ['abc', 'acb', 'bac', 'bca', 'cab', 'cba']))